.out{
    width :  100%;
    background-color: rgba(245, 244, 238, 0.719);
}

.search{
    width: 100%;  
    height: 36px;
    text-align: center;
    background-image: url("./png/taobao.png");
    background-repeat: no-repeat;
    background-size: 27px 25px;
    background-position: 9px center;
    background-color: rgba(235, 97, 17, 0.774);
}
.searchin{
    top: 5px;
    left: 18px;
    width: 330px;
    height: 25px;
    color: white;
    margin: 0 auto;
    border-radius: 7px;
    position: relative;
    background-color: rgb(247, 65, 19);
}
.in1{
    height: 6px;
}
.searchin span{
    margin-top: 5px;
    font-size: 14px;
    font-weight: bold;
    font-family: '宋体';
    position: relative;
    top: -2px;
}

.middle{
    width: 100%;
    text-align: center;
}

.middle img{
    top: 12px;
    width: 160px;
    height: 20px;
    position: relative;
}

.items{
    margin-top: 13px;
    width: 100%;
}

.left{
    width: 170px;
    height: 260px;
    margin-left: 10px;
    left: 4px;
    top: 5px;
    margin-top: 10px;
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
    float: left;
    position: relative;
}

.left img{
    width: 100%;
    height: 190px;
} 

.right{
    width: 170px;
    height: 260px;
    right: 13px;
    top: 5px;
    margin-top: 10px;
    border-radius: 10px;
    background-color: white;
    overflow: hidden;
    float: right;
    position: relative;
}

.right img{
    width: 100%;
    height: 190px;
} 

.commodity{
    top: 3px;
    left: 12px;
    width: 150px;
    color: black;
    font-size: 14px;
    font-family: '宋体';
    display: block;
    position: relative;
}

.money{
    top: 5px;
    bottom: 8px;
    left: 12px;
    width: 150px;
    color: rgb(241, 123, 12);
    font-size: 17px;
    font-family: '宋体';
    display: block;
    position: relative;
}

.last{
    width: 100%;
    height: 80px;
    background-color: rgba(245, 244, 238, 0.719);
    float: left;
}